<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <div class="iphoneframe-container" ref="iphone">
        <router-link to="/account">
          <BackButton />
        </router-link>
        <transition name="fade-top">
          <PreviewCardWithIphoneFrame
            class="w-100"
            :image="previewCardImage"
            :title="previewCardTitle"
            :titleMyan="previewCardTitleMyan"
            :desc="previewCardDesc"
            :descMyan="previewCardDescMyan"
            :isFindOther="true"
            :isContactUs="true"
            type="support"
            firstButtonText="Consult with us"
            secondButtonText="Technical Answer"
            thirdButtonText="Contact us"
            v-show="showPreview"
          />
        </transition>
        <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
          <button
            class="btn btn-circle"
            style="width: 40px; height: 40px;background:#B7B7B7;"
            @click="showPreview = false"
          >
            <CloseIcon class="close-icon" />
          </button>
        </div>

        <div class="mt-4 w-100 title">
          <StageTitle stageNum="6" :title="stageInfoText.title" :desc="stageInfoText.desc" />
        </div>
        <div class="mt-4 w-100 p-3">
          <AddedForm
            v-model="supportInfo"
            :info="info"
            type="support"
            @add="addTechnicalSupportInformation"
            @uploadPhoto="uploadPhoto"
          />
        </div>
        <div class="w-100 d-flex justify-content-center">
          <div class="d-flex flex-center btn btn-primary" @click="Preview()">
            <EyeIcon class="eye-icon" />
            <span class="ml-1">Preview</span>
          </div>
        </div>
        <!--<div class="mt-2 w-100 py-4 px-5">
          <MobileToggle
            :title="toggles.askCustomerInfomation.title"
            :desc="toggles.askCustomerInfomation.desc"
            v-model="isEnabledAskCustomerInfo"
            @change="addShopNature"
          />
        </div> -->
        <div class="mt-4 w-100 d-flex flex-center">
          <router-link to="/account/promote">
            <ForwardButton @click.native="$emit('showPage',1)" />
          </router-link>
        </div>
      </div>
    </div>
    <bottom-navigation-bar />
    <Loading v-if="showLoading" />
    <AlertBox v-if="!shopExist" :alertInfo="alertInfo" @goto="route('/account/contactinfo')" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import ForwardButton from "@/components/ForwardButton";
import CloseIcon from "@/assets/icons/Close";
import EyeIcon from "@/assets/icons/Eye";
import StageTitle from "@/components/StageTitle";
//import MobileToggle from "@/components/MobileToggle";
import AddedForm from "@/components/AddedForm";
import axios from "axios";
import BottomNavigationBar from "@/components/BottomNavigationBar";
import { debounce } from "lodash";
import AlertBox from "@/components/AlertBox";
import Loading from "@/components/Loading";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    //MobileToggle,
    BackButton,
    ForwardButton,
    AddedForm,
    CloseIcon,
    Loading,
    EyeIcon,
    AlertBox,
    BottomNavigationBar
  },
  data() {
    return {
      showLoading: true,
      showPreview: false,
      pageActive: true,
      supportID:'',
      isEnabledAskCustomerInfo: false,
      photoUploading: false,
      previewCardDefaults: {
        title: "Your technical support is awesome",
        desc:
          "Let's us know your technical support on our service.We do appreciate it",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
      },
      stageInfoText: {
        title: "'Technical support' information",
        desc: "Fill your technical support information once"
      },
      toggles: {
        askCustomerInfomation: {
          title: "Ask Customer Information",
          desc:
            "Enable this option will ask the customer name, contact number and address."
        }
      },
      supportInfo: {
        title: "",
        desc: "",
        image: "",
        titleMyan: "",
        descMyan: "",
        fbPageUrl: ""
      },
      info: {
        title: "Type 'Technical support information' title (Eng)",
        titleMyan: "Type 'Technical support information' title (Myan)",
        desc: "Type 'Technical support information' message (Eng)",
        descMyan: "Type 'Technical support information' message (Myan)",
        photoText: "add 'Technical support information' image",
        fbPageUrl: "Type Facebook page url",
        type: "TechnicalSupportInformation",
        buttons: [
          {
            title: "Consult with us"
          },
          {
            title: "Technical Answer"
          },
          {
            title: "Contact us"
          }
        ]
      },
      alertInfo: {
        title: "Alert",
        msg:
          "To fill your technical support information,you need to add contact information first.",
        buttons: [
          {
            text: "Add Shop",
            action: "goto"
          }
        ]
      }
    };
  },
  methods: {
    addTechnicalSupportInformation: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.supportID,
        Title: this.supportInfo.title,
        Message: this.supportInfo.desc,
        ShopGuid: this.shopID
      };
      if (this.supportID == "") {
        await axios.post("technicalSupport/upsert", obj).then(res => {
          this.supportID=res.data;
        });
      } else {
        await axios.put("technicalSupport/upsert", obj);
      }
    }, 3000),
    async uploadPhoto(url) {
      let obj = {
        Guid: this.supportID,
        Photo: url,
        ShopGuid: this.shopID
      };
      this.photoUploading = true;
      if (this.supportID == "") {
        await axios.post("technicalSupport/uploadphoto", obj).then(res => {
          this.photoUploading = false;
          this.supportID=res.data;
        });
      } else {
        await axios.put("technicalSupport/uploadphoto", obj).then(() => {
          this.photoUploading = false;
        });
      }
    },
    async getSupport() {
      if (this.shopID == "" || this.shopID == "0") return;
      await axios
        .get(
          `technicalSupport?shopGuid=${this.shopID}&customerID=${this.customerID}`
        )
        .then(res => {
          if (res.data != null) {
            this.supportInfo.title = res.data.tSupport.Title;
            this.supportInfo.image = res.data.tSupport.PhotoUrl;
            this.supportInfo.desc = res.data.tSupport.Message;
            this.supportID=res.data.tSupport.Guid;
            this.isEnabledAskCustomerInfo = res.data.isEnabledAskCustInfo;
          }
          this.showLoading = false;
        });
    },
    addShopNature: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.shopID,
        CustomerID: this.customerID,
        IsEnabledCustomerInfoForTechSupport: this.isEnabledAskCustomerInfo
      };
      await axios.put("shop/shopNatureFromStepSix", obj);
    }, 3000),
    validateForm() {
      if (
        this.supportInfo.title == "" &&
        this.supportInfo.desc == "" &&
        this.supportInfo.image == ""
      ) {
        return false;
      }
      return true;
    },
    Preview() {
      this.$refs.iphone.scrollIntoView();
      this.showPreview = true;
    }
  },
  mounted() {
    this.getSupport();
  },
  watch:{
    shopID(){
      this.getSupport();
    }
  },
  computed: {
    previewCardTitle() {
      return this.supportInfo.title
        ? this.supportInfo.title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.supportInfo.desc
        ? this.supportInfo.desc
        : this.previewCardDefaults.desc;
    },
    previewCardTitleMyan() {
      return this.supportInfo.titleMyan
        ? this.supportInfo.titleMyan
        : this.previewCardDefaults.title;
    },
    previewCardDescMyan() {
      return this.supportInfo.descMyan
        ? this.supportInfo.descMyan
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.supportInfo.image
        ? this.supportInfo.image
        : this.previewCardDefaults.image;
    },
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    shopExist() {
      return this.shopID != "0";
    },
  }
};
</script>
<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
.page {
  flex: 1;
  overflow-y: auto;
}
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.title {
  padding-left: 0.8rem;
}
.btn-primary {
  border-radius: 40px;
  background-color: $lightBlue;
  border-color: unset;
  &:hover {
    border-color: unset;
    box-shadow: none;
  }
  &:active {
    border-color: unset;
    box-shadow: none;
  }
  &:focus {
    border-color: unset;
    box-shadow: none;
  }
}
.eye-icon {
  width: 0.9rem;
  height: auto;
  margin-top: -2px;
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
.close-icon {
  width: 0.8rem;
}
</style>
